<template>
  <div class="counter">
    <div class='polls'>{{ emoji.get('ballot_box_with_ballot') }}</div>
    <div class='number'>
      <i-count-up
        v-if='poll'
        v-bind:start='poll'
        v-bind:end='end'
        :decimals='0'
        v-bind:duration='duration'
        :options='options'
        :callback='callback'
      ></i-count-up>
    </div>
  </div>
</template>

<script>
import emoji from 'node-emoji';
import config from '../config/';
import _ from 'lodash';
import ICountUp from 'vue-countup-v2';

export default {
  components: {
    ICountUp
  },
  data() {
    return {
      poll: 0,
      end: 0,
      emoji: emoji,
      duration: config.timer,
      options: {
        useEasing: true,
        useGrouping: true,
        separator: ',',
        decimal: '.',
        prefix: '',
        suffix: ''
      }
    }
  },
  created () {
    this.startPolling()
  },
  methods: {
    callback(ins) {
      // this.poll = ins.endVal;
    },
    fetchData(first) {
      this.$http({ url: `${config.baseUrl}poll`, method: 'GET', emulateJSON: true })
        .then((response) => {
          let count = _.toNumber(response.body.totalPollHits);
          if (count > 10000) {
            count = count / 1000;
            this.options.suffix = 'к';
          }

          if (first) this.poll = count - count / 5; this.end = count;
          this.end = count;
        })
    },
    startPolling() {
      this.fetchData(true);
      setInterval(() => this.fetchData(), config.timerMS); 
    }
  }
}
</script>

<style lang='scss'>
  .counter {
    .polls {
        font-size: 9vh;
        font-weight: bold;
        display: inline-block;
        vertical-align: bottom;
        color: #e3e3e3;
        margin: 0 1vw;
        line-height: 1;
    }
    .number {
      line-height: 1;
      font-family: 'Avenir', Helvetica, Arial, sans-serif;
      display: inline-block;
      font-weight: bold; 
      font-style: normal; 
      font-size: 4rem;
      color: #fff;
      text-align: center;
    }
  }
</style>



